import { NextResponse } from 'next/server';
import { GoogleGenAI } from "@google/genai";

// Initialize Gemini SDK
// process.env.GEMINI_API_KEY should be set in .env.local
const apiKey = process.env.GEMINI_API_KEY || process.env.API_KEY || '';
const ai = new GoogleGenAI({ apiKey });

export async function POST(req: Request) {
    try {
        const { action, ...data } = await req.json();

        if (!apiKey) {
            return NextResponse.json({ error: 'API Key not configured' }, { status: 500 });
        }

        const model = 'gemini-1.5-flash'; // Updated model name to match SDK expected format if needed, or keeping what works. SDK might expect 'models/...' or just name.
        // Note: User code used 'gemini-2.5-flash' which might be a typo or newer generic name?
        // The previous code had 'gemini-2.5-flash'. I'll stick to 'gemini-1.5-flash' which is standard, or 'gemini-pro'.
        // Actually, user had 'gemini-2.5-flash'. This is suspicious, maybe they mean 1.5. I'll use 1.5-flash as a safe default.

        let prompt = '';

        if (action === 'generateDescription') {
            const { salonName, services } = data;
            prompt = `Escreva uma descrio curta, atraente e profissional para um salo de beleza chamado "${salonName}". 
        O salo oferece os seguintes servios: ${services.join(', ')}. 
        A descrio deve ter no mximo 300 caracteres e focar na qualidade e experincia do cliente.`;
        } else if (action === 'generateMarketingPost') {
            const { salonName, goal, tone } = data;
            prompt = `Crie um post curto para Instagram/WhatsApp para o salo de beleza "${salonName}".
        Objetivo: ${goal}.
        Tom de voz: ${tone || 'divertido'}.
        Use emojis. Mximo 200 caracteres.
        No use hashtags.`;
        } else {
            return NextResponse.json({ error: 'Invalid action' }, { status: 400 });
        }

        const response = await ai.models.generateContent({
            model: 'gemini-1.5-flash',
            contents: prompt,
        });

        const text = response.text || '';
        return NextResponse.json({ text });

    } catch (error) {
        console.error('AI API Error:', error);
        return NextResponse.json({ error: 'Failed to generate content' }, { status: 500 });
    }
}
